/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"That's Me in the Corner","date":"03 Nov 2006","slug":"/2006-11-03-thats-me-in-the-corner/","tags":"Y2006"},"html":"<p>Another pitfall of telecommuting is that they start an internal blog at the office and you don't find out about it until 6 weeks later (no, it's not <a href=\"http://www.technorat.de/\">Technorat</a>). They've been posting profiles of various co-workers, and since I doubt that <a href=\"http://poolie.kulando.de/\">Poolie</a> will come to Hamburg to interview me, I've decided to save him the trouble.</p>\n<p><strong>Your Name:</strong><br />\nScott Hanson</p>\n<p><strong>Your Email address:</strong><br />\nEmail is dead. <a href=\"http://mabber.com/\">Mabber me</a> at shanson@mabber.com</p>\n<p><strong>Your Age:</strong><br />\n44</p>\n<p><strong>What do you do for nu2m?</strong><br />\nSysadmin and network plumber in Hamburg, part-time, home-office.</p>\n<p><strong>Which page or feed do you read first in the morning?</strong><br />\n<a href=\"http://www.overheardinnewyork.com/\">Overheard in New York</a>.</p>\n<p><strong>Last comic read?</strong><br />\nThis week's Mickey Mouse (out loud for my son Christopher)</p>\n<p><strong>Murderer or Objector (Mörder oder Zivi)?</strong><br />\nNeither. I refused to register with Selective Service, if that counts.</p>\n<p><strong>Do you subscribe to any magzines?</strong><br />\nThe Economist</p>\n<p><strong>Your favorite color?</strong><br />\nNone. I'm color-blind, my reality is different than yours.</p>\n<p><strong>Musical taste?</strong><br />\nNot as progressive as I used to be. My earbuds are black.</p>\n<p><strong>What do you play?</strong><br />\nMau-mau, Lego Star Wars. And the fool.</p>\n<p><strong>What do you do when you are not at the computer?</strong><br />\nI play taxi-driver for my son. And with my son.</p>\n<p><strong>Color or black and white?</strong><br />\nWhatever the director wanted.</p>\n<p><strong>Where have you been on vacation?</strong><br />\nLake Superior, Monument Valley, San Diego, Florida Gulf Coast. Mallorca, Prague, Budapest, Istria, Algarve, Leogang, Sylt, Legoland.</p>\n<p><strong>Coffee or tea?</strong><br />\nEspresso with milk.</p>\n<p><strong>Wireless oder cable mouse?</strong><br />\nBluetooth, with two buttons and a wheel.</p>\n<p><strong>Favorite clothing color?</strong><br />\nBlack. I know how it looks.</p>\n<p><strong>Favorite perfume?</strong><br />\nJil Sander Sun, but only on my wife.</p>\n<p><strong>Which CD and DVD did you last buy?</strong><br />\nDo rentals and allofmp3 count?<br />\nFor Christopher: Räuber Hotzenplotz, Sponge Bob Season 3<br />\nFor myself: Rosenstolz 'Das Grosse Leben' and Justin Timberlake 'Future Sex Love Sounds', Corpse Bride</p>\n<p><strong>Who can you laugh at?</strong><br />\nMyself. Usually.</p>\n<p><strong>What is on your desktop?</strong><br />\nMy desk is our kitchen table. In addition to my MacBook there is a flower pot, a sharp knife, several candles, and salt and pepper shakers.</p>\n<p><strong>Your last meal:</strong><br />\nHold on, I'm still looking at the menu...</p>\n<p><strong>What is your favorite TV series?</strong><br />\nNo time for TV. Who killed Laura Palmer anyway?</p>\n<p><strong>How many pillows do you sleep with?</strong><br />\nOne, but that's probably not enough.</p>\n<p><strong>Hotel or Camping?</strong><br />\nHotel, unless we're looking for adventure.</p>\n<p><strong>Your motto?</strong><br />\nEverything depends on your point of view.</p>\n<p><strong>Who is Herr Latz?</strong><br />\nNo idea.</p>\n<p><strong>Famous last words:</strong><br />\nRosebud</p>"},"next":{"fields":{"title":"Foreign Holidays","date":"01 Nov 2006","slug":"/2006-11-01-foreign-holidays/"}},"previous":{"fields":{"title":"Macaca!","date":"08 Nov 2006","slug":"/2006-11-08-macaca/"}}},{"node":{"fields":{"title":"Foreign Holidays","date":"01 Nov 2006","slug":"/2006-11-01-foreign-holidays/","tags":"Y2006"},"html":"<p>One of the pitfalls of telecommuting is that you have no idea where your co-workers are. I just now, at 11:30 am, realized that today is a holiday in Cologne, which explains why no one is sending me any mail or Jabber messages today. They may have more days off in Cologne, but the higher quality of life in Hamburg more than makes up for it.</p>\n<p>Speaking of quality of life, Hamburg just had the highest storm tide in 15 years (5.90 meters, which is actually 20 cm higher than the killer flood of 1962, if you're looking for evidence of global warming). If you parked on the Fischmarkt this morning, the inconvenient truth is that your car is now floating in the Elbe (or perhaps more inconvenient, has been impounded by the police).</p>\n<p>Update: The flood was actually a meter lower than expected, so no record. They still towed away lots of cars, though.</p>"},"next":{"fields":{"title":"Expat Pitfalls","date":"31 Oct 2006","slug":"/2006-10-31-expat-pitfalls/"}},"previous":{"fields":{"title":"That's Me in the Corner","date":"03 Nov 2006","slug":"/2006-11-03-thats-me-in-the-corner/"}}},{"node":{"fields":{"title":"Expat Pitfalls","date":"31 Oct 2006","slug":"/2006-10-31-expat-pitfalls/","tags":"Y2006"},"html":"<p>Slate, <a href=\"http://www.slate.com/id/2152088/entry/2152191/?nav=tap3\">\"Henry Miller School of Overseas Living for Misanthropes\"</a>:</p>\n<blockquote><p>\n  Expat scenes invariably have plenty of writers and artists but a curiously scant quantity of writing and art. This isn't a new phenomenon: Ernest Hemingway alluded to it in The Sun Also Rises, when Bill Gorton jestingly upbraids Jake Barnes: \"You drink yourself to death,\" he says. \"You become obsessed with sex. You spend all your time talking, not working. You are an expatriate, see? You hang around cafes.\" George Orwell made a similar observation in \"Inside the Whale\" (an essay-length riff on Miller's Tropic of Cancer), pointing out that expatriate writers are disproportionately obsessed with \"drinking, talking, meditating, and fornicating.\"\n</p></blockquote>\n<p>I must be hanging out in the wrong cafés. What, no whining? (via <a href=\"http://www.anglofritz.com/2006/10/refuting_the_suckage.html\">anglofritz</a>)</p>"},"next":{"fields":{"title":"The Bastardization of German Culture","date":"30 Oct 2006","slug":"/2006-10-30-the-bastardization-of-german-culture/"}},"previous":{"fields":{"title":"Foreign Holidays","date":"01 Nov 2006","slug":"/2006-11-01-foreign-holidays/"}}}],"pathPrefix":"","first":false,"last":false,"index":181,"pageCount":1101}}

/***/ })

